import React from 'react'
import {
    CButton,
    CCard,
    CCardBody,
    CCardFooter,
    CCardHeader,
    CCol,
    CForm,
    CFormGroup,
    CFormText,
    CTextarea,
    CInput,
    CInputFile,
    CInputCheckbox,
    CInputRadio,
    CLabel,
    CSelect,
    CRow,
    CSwitch
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { DocsLink } from 'src/reusable'
import { useSelector } from 'react-redux'

function AddProduct() {
    const [collapsed, setCollapsed] = React.useState(true)
    const [showElements, setShowElements] = React.useState(true)
    const [ProductName, setProductName] = React.useState("")
    const [ProductPrice, setProductPrice] = React.useState("")
    const [SelectedCatagory, setSelectedCatagory] = React.useState("")
    

    const userEmail = useSelector(state => state.user.email)
    return (
        <CRow>
            <CCol xs="12" md="12">
                <CCard>
                    <CCardHeader>
                        Add Product
                        <small> Inventory</small>
                    </CCardHeader>
                    <CCardBody>
                        <CForm action="" method="post" encType="multipart/form-data" className="form-horizontal">
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel>Email</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <p className="form-control-static">{userEmail && userEmail}</p>
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel htmlFor="text-input">Product Name</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <CInput id="text-input" name="text-input" placeholder="Text" />
                                    <CFormText>Product Display Name</CFormText>
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel htmlFor="date-input">Expire Date (Optional)</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <CInput type="date" id="date-input" name="date-input" placeholder="date" />
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel htmlFor="textarea-input">Product Description</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <CTextarea
                                        name="textarea-input"
                                        id="textarea-input"
                                        rows="9"
                                        placeholder="Content..."
                                    />
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel htmlFor="select">Product Catagory</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <CSelect custom name="select" id="select">
                                        <option value="0">Please select</option>
                                        <option value="1">Cosmetics</option>
                                        <option value="2">Perfumes</option>
                                        <option value="3">Watches</option>
                                    </CSelect>
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol tag="label" sm="3" className="col-form-label">
                                    In Stock
                                </CCol>
                                <CCol sm="9">
                                    <CSwitch
                                        className="mr-1"
                                        color="primary"
                                        defaultChecked
                                        shape="pill"
                                    />
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel>Attributes</CLabel>
                                </CCol>
                                <CCol md="9">
                                    <CFormGroup variant="custom-checkbox" inline>
                                        <CInputCheckbox
                                            custom
                                            id="inline-checkbox1"
                                            name="inline-checkbox1"
                                            value="option1"
                                        />
                                        <CLabel variant="custom-checkbox" htmlFor="inline-checkbox1">Wearables</CLabel>
                                    </CFormGroup>
                                    <CFormGroup variant="custom-checkbox" inline>
                                        <CInputCheckbox custom id="inline-checkbox2" name="inline-checkbox2" value="option2" />
                                        <CLabel variant="custom-checkbox" htmlFor="inline-checkbox2">Beauty</CLabel>
                                    </CFormGroup>
                                    <CFormGroup variant="custom-checkbox" inline>
                                        <CInputCheckbox custom id="inline-checkbox3" name="inline-checkbox3" value="option3" />
                                        <CLabel variant="custom-checkbox" htmlFor="inline-checkbox3">Edible</CLabel>
                                    </CFormGroup>
                                    <CFormGroup variant="custom-checkbox" inline>
                                        <CInputCheckbox custom id="inline-checkbox3" name="inline-checkbox3" value="option3" />
                                        <CLabel variant="custom-checkbox" htmlFor="inline-checkbox3">Not for children</CLabel>
                                    </CFormGroup>
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CCol md="3">
                                    <CLabel htmlFor="text-input">Amount in Stock</CLabel>
                                </CCol>
                                <CCol xs="12" md="9">
                                    <CInput id="text-input" type="number" name="text-input" placeholder="Amount in numbers" />
                                    <CFormText>Enter amount less than 1000</CFormText>
                                </CCol>
                            </CFormGroup>
                            <CFormGroup row>
                                <CLabel col md={3}>Product Image (Optional)</CLabel>
                                <CCol xs="12" md="9">
                                    <CInputFile custom id="custom-file-input" />
                                    <CLabel htmlFor="custom-file-input" variant="custom-file">
                                        Choose file...
                                    </CLabel>
                                </CCol>
                            </CFormGroup>
                        </CForm>
                    </CCardBody>
                    <CCardFooter>
                        <CButton type="submit" size="sm" color="primary"><CIcon name="cil-scrubber" /> Submit</CButton>
                        <CButton type="reset" size="sm" color="danger"><CIcon name="cil-ban" /> Reset</CButton>
                    </CCardFooter>
                </CCard>
            </CCol>
        </CRow>
    )
}

export default AddProduct
